export async function equipments() {
  return new Promise((rej) => {
    rej({
      state: "00",
      data: [
        { a: "아리온제주1" },
        { a: "아리온제주2" },
        { a: "아리온제주3" },
        { a: "아리온제주4" },
        { a: "아리온제주5" },
        { a: "아리온제주6" },
        { a: "아리온제주7" },
      ],
    });
  });
}
export async function createEquipment(body) {
  return new Promise((rej) => {
    rej({
      state: "00",
      data: body,
    });
  });
}

